.product-detail {
  display: grid;
  padding: 5rem 14rem;
  margin: 0 auto;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 3rem;
  max-width: 144rem;

  .product-images {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    .preview-img-block {
      display: block;
    }
    .main-image {
      grid-column: 1 / span 2;
      overflow: hidden;
      border-radius: 1rem;
      img {
        width: 100%;
        height: 40rem;
      }
    }

    .preview-image {
      img {
        width: 23%;
        height: 12rem;
        display: block;
        margin: auto;
        cursor: pointer;

        &:hover {
          opacity: 0.8;
        }
      }
    }
  }

  .product-details {
    display: flex;
    flex-direction: column;
    width: max-content;
    .product-info {
      display: flex;
      flex-direction: column;
      justify-content: left;
      align-items: start;

      .logo {
        background: transparent 0% 0% no-repeat padding-box;
        margin-bottom: 1rem;
        img {
          height: 12rem;
          width: 12rem;
          display: block;
        }
      }

      .product-name {
        font-size: 1.5rem;
        font-weight: bold;
        margin-bottom: 1rem;
        text-align: center;
      }
      .product-price {
        display: flex;
        margin-bottom: 2rem;
        gap: 1.5rem;
        align-items: center;
        justify-content: space-between;
      }
      .product-current-price {
        color: #542e90;
        font-weight: 700;
        font-size: 2rem;
      }
      .product-discount-price {
        text-decoration: line-through;
        color: #a7a9ac;
        font-weight: 400;
        font-size: 1.5rem;
      }
      .product-discount-percentage {
        background: #fff200 0% 0% no-repeat padding-box;
        border-radius: 0.25rem;
        padding: 0.5rem;
        color: #000;
        font-weight: 600;
      }
      .product-category {
        font-size: 1rem;
        font-weight: bold;
        margin-bottom: 1rem;
        color: #a7a9ac;
      }
      .product-rate {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 2rem;
        font-weight: 700;
        color: #000;
        font-size: 1.5rem;
        margin-bottom: 2rem;
        margin-top: 1rem;
      }

      .product-description {
        margin-bottom: 1rem;
        margin-top: 2rem;
        font-weight: 700;
        font-size: 2rem;
        color: #1d1d1b;
        letter-spacing: 0px;
      }
      .product-actions {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 1rem;

        .add-to-cart-btn {
          background: #542e90 0% 0% no-repeat padding-box;
          color: white;
          border: none;
          border-radius: 1.8rem;
          font-size: 1.2rem;
          font-weight: bold;
          padding: 1rem 2rem;
          cursor: pointer;
          transition: all 0.3s ease-in-out;

          &:hover {
            background: #542e90 0% 0% no-repeat padding-box;
            transform: scale(1.1);
          }
        }
        .pick-from-store-btn {
          background: #fff200 0% 0% no-repeat padding-box;
          color: black;
          border: none;
          border-radius: 1.8rem;
          font-size: 1.2rem;
          font-weight: bold;
          padding: 1rem 2rem;
          cursor: pointer;
          transition: all 0.3s ease-in-out;

          &:hover {
            background: #fff200 0% 0% no-repeat padding-box;
            transform: scale(1.1);
          }
        }
      }
    }
  }
}

.preview-images {
  display: flex;
  gap: 1rem;
}

.arrows-icons {
  display: block;
  align-self: center;
}
